import Vue from 'vue'
/*     
    processar - const item = await this.$carregar(5000,'dark')
    monstrar - await this.$monstrar(item,'titulo','desc','danger')  
    erro -  this.$error('Houve um erro!',`- ${error}`)

 */

Vue.mixin({

    computed:{

        $tema:function () {
            
            return this.$vuetify.theme.dark?'dark':'light'
        }

    },

    methods: {
      $monstrar(item,title,text,color) {
        try {
            
            return new Promise((resolve,reject)=>{
                
                item.loading = false
                item.color = color,
                item.title = title,
                item.text = text,
                
                
                resolve(true)
            })
        } catch (error) {
         
            this.$error('Houve um erro!',`- ${error}`)

            
        }
            
  
     },
     $carregar(tmp = Number(),color){

        return new Promise((resolve,reject)=>{

            resolve(   
           
                this.$vs.notification({
                position: 'bottom',
                color: color,
                title: '',
                text: '',
                square:true,
                progressAuto:true,
                // progress:progress,
                duration: tmp,
                width:'',
                loading:true,
                onClick:()=>{}
              })  
              
              )





        })


     },

     $show(title,text,tmp,color){

        return new Promise((resolve,reject)=>{

            resolve(   
           
                this.$vs.notification({
                position: 'bottom',
                color: color,
                title: title,
                text: text,
                square:true,
                progressAuto:true,
                // progress:progress,
                duration: tmp,
                width:'',
                loading:false,
                onClick:()=>{}
              })  
              
              )





        })


     },

     $error(title,causa){

        this.$vs.notification({
            position: 'bottom',
            color: 'danger',
            title: title,
            text: causa,
            square:true,
            progressAuto:true,
            // progress:progress,
            duration: 3000,
            width:'',
            loading:false,
            onClick:()=>{}
          })


     }
    },
    created() {
            
    //    console.log(this)

    },
  })


  